*{
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
    padding: 0;
    margin: 0;
    background: black;
    
    
    
    
    
    
    
}
html{
    font-size: 1.3rem;
}

body{
    
    
    width:100vw;
    height: 100vh;
   
}

    
.contactus .locationimg .Mapssize{
    color: rgba(19, 185, 185, 0.603);
    font-size: 2rem;
}

header{
   
    position: fixed;
    top: 0;
    
    width: 100%;
    display: flex;
    justify-content: space-between;
    
    align-items: center;
    transition: 0.6s;
    opacity: 0.9;
    
    
    padding: 5px 100px;
    
    z-index: 10000;
}
header .logo{
    text-decoration: none;
    font-size: 2em;
    color: chocolate;
    font-weight: 370;
    text-transform: uppercase;
    position: relative;
    top:0;
    transition: 0.6s;
    letter-spacing: 2px;
}

header ul{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    
    
    
    

}
header ul li{
    list-style: none;
    
    
    padding: 12px 7px;
    
}
header ul li a {
    position: relative;
    top: 0;
    
    text-decoration: none;
    margin: 0 10px;
    color: #fff;
    
    letter-spacing: 1px;
    font-weight: lighter;
    transition: 0.6s;
    
    
}






.navigationbar{
    padding-bottom: 4%;
} 
header .logo:hover {
    
    
    color: cadetblue;
    
    transform: rotateY(30deg);
    transition: 0.2s;
}
header ul li:hover{
    
    transform: rotateY(30deg);
    transition: 0.2s;
}
header ul li a:hover{
    color: cadetblue;
}


.carousel{
    display: block;
    margin: 0 auto;
    position: relative;
    top: 100;
    overflow: hidden;
    margin: 70 auto;
    width: 100%;
    padding-top: 116px;
    
    
    


    
    

}
.mainCarouselContainer{
    margin: 0 90px;
}
.carousel figure{
   
    position: relative;
    left: 0;
    width: 500%;
    float: left;
    animation: 20s slider infinite;

}
.carousel figure img{
    width: 20%;
    float: left;
    height: 88%;
}
@keyframes slider{
    0%{
        left: 0%;
    }
    20%{
        left: 0%;

    }
    25%{
        left: -100%;
    }
    40%{
        left: -100%;
    }
    50%{
        left: -200%;
    }
    60%{
        left: -200%;
    }
    75%{
        left: -300%;
    }
    95%{
        left: -300%;
    }
    100%{
        left: -400%;
    }
}


