@media (max-width:600px) and (max-width:400px) {
    *{
   
        height: 100vh;
        width:100%;
        padding-top: 42%;
        background-size: cover;
    }
}

@media (max-width:375px){
    .carousel{
        width: 100%;
    }
}

@media (max-width:400px){
    .contactus .locationimg{
        width: 100vw;
        height: 100vh;
        padding-top: 42%;
        margin: 0 auto;
        
    }
}

